import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import { VideoData, VideoDetail, VideoDetailService, VideoItem, VideoLikeService, VideoService } from "../services/VideoService";

class VideoStore {
  public list: VideoItem[] = [];
  public loading: boolean = false;
  public loadingDetail: boolean = false;
  private _page = 1;
  public loadingMore: boolean = false;
  public liking: boolean = false;
  public noMore: boolean = false;
  public type:string = 'free';
  public videoDetail?: VideoDetail;
  public selectedItem?: VideoItem;
  public showlist: VideoItem[] = [];

  constructor() {
    makeAutoObservable(this);
  }

  public async getData(loadmore: boolean = false,size:number = 16) {
    if (this.loading || this.loadingMore) {
      return;
    }
    let response: VideoData;

    if (loadmore) {
      if (this.noMore) {
        return;
      }
      this.loadingMore = true;
      this._page += 1;
    } else {
      this.loading = true;
      this._page = 1;
    }
    try {
      response = await new VideoService(this._page,size).send();
    } catch (e: any) {
      this.loading = false;
      this.loadingMore = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.loadingMore = false;

    if (loadmore) {
      this.noMore = response.list.length ==0;
      this.list.push(...response.list);
    } else {
      this.list = response.list;
    }



  }

  public async getDetail(id: any) {
    let response: VideoDetail;
    this.loadingDetail = true;
    this._page = 1;
    try {
      response = await new VideoDetailService(id).send();
    } catch (e: any) {
      this.loadingDetail = false;
      showError(e);
      return;
    }
    this.loadingDetail = false;
    this.videoDetail = response;
  }


  public async like() {
    if(this.liking){
      return ;
    }
    this.liking=true;
    if (this.videoDetail) {
      this.videoDetail.islike = this.videoDetail.islike == 1 ? 0 : 1;
      let count = parseInt(this.videoDetail.likecount);
      if (this.videoDetail.islike == 1) {
        count += 1;
      } else {
        count -= 1;
      }
      this.videoDetail.likecount = count.toString();
    }
    try {
      await new VideoLikeService(this.videoDetail?.id, this.videoDetail?.islike).send();
    } catch (e: any) {
      if (this.videoDetail) {
        this.videoDetail.islike = this.videoDetail.islike == 1 ? 0 : 1;
        let count = parseInt(this.videoDetail.likecount);
        if (this.videoDetail.islike == 1) {
          count += 1;
        } else {
          count += 1;
        }
        this.videoDetail.likecount = count.toString();
      }
      showError(e);
    this.liking=false;
    return;
    }
    this.liking=false;
  }


}

const videoStore = new VideoStore();

export { videoStore };