import { observer } from 'mobx-react';
import './Main.scss';
import { useEffect } from 'react';
import 'react-tiga-swiper/dist/index.css';
import { videoStore } from '../../stores/VideoStore';
import { researchStore } from '../../stores/ResearchStore';
import ResearchItemPage from '../Research/ResearchItem';
import GlobalConfig from '../../config/GlobalConfig';
import { Icon_more } from '../../images/index/rightArrow';
import VideoItemPage from '../Videos/VideoItem';
import Translate from '../../components/Text';


export const Main = observer(() => {
  useEffect(() => {
    videoStore.getData(false, 6);
    researchStore.getHot();
  }, [])
  return (
    <div className="mainWrapper">
      <div className="videoWrapper">
        <div className="top">
          <div className="title"><Translate text='home.d' /></div>
          <div className="more" onClick={() => {
            GlobalConfig.history.push('/videos')
          }}>
            <div className="text"><Translate text='common.b' /></div>
            <img src={Icon_more} alt="" />
          </div>
        </div>
        <div className="list">
          {videoStore.list.slice(0, 6).map((l, i) => <div className="item" key={i}> <VideoItemPage isWhite={true} item={l} /></div>)}
        </div>
      </div>
      <div className="newsWrapper">
        <div className="title"><Translate text='home.b' /></div>
        <div className="list">
          {researchStore.hotList.map((l, i) => <div className="item" key={i}>
            <ResearchItemPage id={i} item={l} />
          </div>
          )}
        </div>
      </div>
    </div>
  );
})


