import { observer } from 'mobx-react';
import './Index.scss';
import { Select } from 'antd';
import { Headers } from '../Header/Header';
import { Top_Bg } from '../../images/indexNew/topBg';
import { translate } from '../../components/Text';
import { Index_img1 } from '../../images/indexNew/img1';
import { Index_img2 } from '../../images/indexNew/img2';
import { Index_img3 } from '../../images/indexNew/img3';
import { Index_download } from '../../images/indexNew/download';
import { Index_img4 } from '../../images/indexNew/img4';
import { Index_img7 } from '../../images/indexNew/img7';
import { Index_img6 } from '../../images/indexNew/img6';
import { Index_img9 } from '../../images/indexNew/img9';
import { Index_img8 } from '../../images/indexNew/img8';
import { Index_img5 } from '../../images/indexNew/img5';
import { Index_img11 } from '../../images/indexNew/img11';
import { Index_img10 } from '../../images/indexNew/img10';
import { Index_logo } from '../../images/indexNew/logo';
import { useEffect, useState } from 'react';
import { Index_QrCode } from '../../images/indexNew/qrcode';
import { mainStore } from '../../stores/MainStore';

export const IndexPage = observer((props: any) => {
  const [style, setStyle] = useState('');
  useEffect(() => {
    var scrollAction: any = { x: 'undefined', y: 'undefined' };
    var scrollDirection: any;
    function scrollFunc() {
      if (typeof scrollAction.x == 'undefined') {
        scrollAction.x = window.pageXOffset;
        scrollAction.y = window.pageYOffset;
      }
      var diffX = scrollAction.x - window.pageXOffset;
      var diffY = scrollAction.y - window.pageYOffset;
      if (diffX < 0) {
        scrollDirection = 'right';
      } else if (diffX > 0) {
        scrollDirection = 'left';
      } else if (diffY < 0) {
        scrollDirection = 'down';
      } else if (diffY > 0) {
        scrollDirection = 'up';
      } else {
        // First scroll event
      }
      scrollAction.x = window.pageXOffset;
      scrollAction.y = window.pageYOffset;
    }
    window.onscroll = function () {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      scrollFunc();
      if (scrollDirection == 'down') {
        //页面向下滚动要做的事情
        if (style != ' hide') {
          setStyle(' hide');
        }
      }
      else if (scrollDirection == 'up') {
        if (scrollTop <= 5) {
          setStyle('');
        } else {
          //页面向上滚动要做的事情
          if (style.indexOf('navbarFloat') == -1) {
            setStyle('show navbarFloat');
          }
        }
      }
    };
  }, []);
  let email = '';
  return (
    <div className="indexWrapper">
      <div className={`head ${style}`}>
        <Headers />
      </div>
      <div className="top" >
        <div className='videoContent'>
          <video src="https://aolinews.oss-ap-southeast-2.aliyuncs.com/videos/topBg.mp4" autoPlay={true} loop={true} muted={true}  >
            您的浏览器不支持 video 标签。
          </video>
        </div>
        <div className="info">
          <div className="name">Oli View</div>
          <div className="slogan">Analyse Smart, Dream Big</div>
          <div className="description" style={{ textAlign: mainStore.isEn ? "center" : "left" }}>{translate('index.a')}</div>
          <div className="description" style={{ marginTop: "15px" }}>{translate('index.b')}</div>
          <div className="discover" onClick={() => {
            props.history.push('/main')
          }}>{translate('index.c')}</div>

          <div className="imgs">
            <div className="img1">
              <img src={Index_img1} alt="" />
            </div>
            <div className="img2">
              <img src={Index_img2} alt="" />
            </div>
            <div className="img3">
              <img src={Index_img3} alt="" />
            </div>
          </div>
        </div>

      </div>
      <div className="download">
        <img className='icon' src={Index_download} alt="" />
        <div className="text">{translate('index.d')}</div>
        <div className="qrcode">
          <img src={Index_QrCode} alt="" />
        </div>
      </div>


      <div className="view">
        <div className="imgs">
          <div className="bg"></div>
          <img className="img1" src={Index_img4} alt="" />
          <img className="img2" src={Index_img5} alt="" />
        </div>
        <div className="infoItem">
          <div className="title">{translate('index.e')}</div>
          <div className="info">{translate('index.f')}</div>
          <div className="more" onClick={() => {
            props.history.push('/main')
          }}>{translate('index.r')}</div>
        </div>
      </div>


      <div className="research">
        <div className="infoItem">
          <div className="title">{translate('index.g')}</div>
          <div className="info">{translate('index.h')}</div>
          <div className="moreWrapper">
            <div className="more" onClick={() => {
              props.history.push('/main/news/index')
            }}>{translate('index.r')}</div>
          </div>
        </div>
        <div className="imgs">
          <div className="bg"></div>
          <img className="img2" src={Index_img7} alt="" />
          <img className="img1" src={Index_img6} alt="" />
        </div>
      </div>


      <div className="finance">
        <div className="imgs">
          <div className="bg"></div>
          <img className="img1" src={Index_img8} alt="" />
          <img className="img2" src={Index_img9} alt="" />
        </div>
        <div className="infoItem">
          <div className="title">{translate('index.i')}</div>
          <div className="info">{translate('index.j')}</div>
          <div className="more" onClick={() => {
            props.history.push('/main/market/index')
          }}>{translate('index.r')}</div>
        </div>
      </div>


      <div className="stock">
        <div className="infoItem">
          <div className="title">{translate('index.k')}</div>
          <div className="info">{translate('index.l')}</div>
          <div className="moreWrapper">
            <div className="more" onClick={() => {
              props.history.push('/main/market/index')
            }}>{translate('index.r')}</div>
          </div>
        </div>
        <div className="imgs">
          <div className="bg"></div>
          <img className="img2" src={Index_img10} alt="" />
          <img className="img1" src={Index_img11} alt="" />
        </div>
      </div>


      <div className="contact">
        <img src={Index_logo} alt="" className="logo" />
        <div className="tips">{translate('index.m')}</div>
        <div className="placeHolder">{translate('index.o')}</div>
        <div className="input">
          <input type="text" onChange={(v)=>{
            email = v.target.value;
            console.log('email',email);
          }}/>
          <div className="subscribe" onClick={()=>{
            mainStore.visitorRegister(email)
          }}>{translate('index.p')}</div>
        </div>
      </div>
    </div>
  );
})


