import { Ajax } from '../utils/http';

export class StockOwnerShipService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<OwnershipData> {
        var response: OwnershipData;
        var data = {
            symbol: this.symbol,
        }
        try {
            response = await new Ajax<OwnershipData>( '/oliview_market_ownerships', data,true).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

/*tsModel6*/
export interface OwnershipData {
  insider_transactions_summary: Insider_transactions_summary;
  insider_transactions: Insider_transactions[];
  ownership: Ownership[];
  fund_ownership: Fund_ownership[];
}


/*Insider_transactions_summary*/
export interface Insider_transactions_summary {
  buy_qty: Object;
  sell_qty: Object;
}

/*Insider_transactions*/
export interface Insider_transactions {
  symbol: string;
  name: string;
  share: number;
  change: number;
  filingDate: string;
  transactionDate: string;
  transactionCode: string;
  transactionPrice: number;
}

/*Ownership*/
export interface Ownership {
  change: number;
  filingDate: string;
  name: string;
  share: number;
  percent: number;
}

/*Fund_ownership*/
export interface Fund_ownership {
  change: number;
  filingDate: string;
  name: string;
  portfolioPercent: number;
  share: number;
  percent: number;
}
