import './Header.scss';
import { observer } from 'mobx-react';
import { Profile } from '../Profile/Profile';
import { userStore } from '../../stores/UserStore';
import '../Research/Research.scss';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import { Icon_logo } from '../../images/index/logo';
import { StockSearchInput } from './SearchStock';
import GlobalConfig from '../../config/GlobalConfig';
import { indexStore, SearchType } from '../../stores/IndexStore';
import Translate from '../../components/Text';
import { ReserchSearchInput } from './SearchResearch';
import { Icon_login } from '../../images/index/login';
import { Select } from 'antd';
import { mainStore } from '../../stores/MainStore';

const { Option } = Select;

export const Headers = observer(() => {
    return (
        <div className="header">
            <div className="logo" onClick={() => {
                GlobalConfig.history.push('/');
            }}>
                <img src={Icon_logo} alt="" />
                {/* <div className="name">Oli Views</div> */}
            </div>
            {indexStore.searchType == SearchType.stock && <StockSearchInput />}
            {indexStore.searchType == SearchType.research && <ReserchSearchInput />}
            <div className='profileContent'>
                <div className="subscribed" onClick={() => {
                    GlobalConfig.history.push('/subscribe');
                }} ><Translate text='mine.subscribe.a' /></div>
                {userStore.userInfo ? <Profile /> : <div>
                    <img className='icon' src={Icon_login} alt="" onClick={() => {
                        loginStore.LoginModel = LoginModel.login;
                        GlobalConfig.history.push('/login')
                    }} />
                </div>
                }

                <div className="language">
                    <Select defaultValue={mainStore.isEn?"EN": "ZH"} style={{ width: '100%' }} bordered={false} onChange={(v) => {
                        mainStore.isEn = v == 'EN';
                    }}>
                        <Option value="ZH">ZH</Option>
                        <Option value="EN">EN</Option>
                    </Select>
                </div>

                {/* <img className='language' onClick={() => {
                    mainStore.isEn = !mainStore.isEn;
                }} src={Icon_language} alt="" /> */}
            </div>
        </div>)
})