import './Videos.scss';
import { videoStore } from '../../stores/VideoStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-awesome-slider/dist/styles.css';
import Icons from '../../images/images';
import AwesomeSlider from 'react-awesome-slider';
import eventBus from '../../utils/eventBus';
import { Icon_Play } from '../../images/icons/Play';
import IntersectionImage from 'react-intersection-image';
import { verifyRule } from '../../utils/globalFuncs';
import { LocalStorageService } from '../../utils/storage';
import { Sub_Bg_Img } from '../../images/SubBg';
import { Loading_icon } from '../../images/Loading';
import { Icon_like } from '../../images/index/like';
import { CommentPage } from '../Creator/Comment';
import { Icon_like_on } from '../../images/index/likeOn';
import { translate } from '../../components/Text';


export const VideosPlayPage = observer((props: any) => {
    let id = props.match.params.id;
    useEffect(() => {
        videoStore.getData(true);
        videoStore.getDetail(id);
    }, []);
    // eventBus.addListener('toBottom', () => {
    //     videoStore.getData(true);
    // })
    return (
        <div className="videoPlayPage">
            {videoStore.loadingDetail ? <div className="loading" >
                <img src={Loading_icon} alt="" />
            </div> : <div className="videoWrapper">
                <div className="leftContent">
                    <div className="playContent">
                        <video controlsList='nodownload' src={videoStore.videoDetail?.url} controls={true} autoPlay={true} muted={true}></video>
                    </div>
                    <div className="info">
                        <div className="text">
                            <div className="title">{videoStore.videoDetail?.title}</div>
                            <div className="view">{videoStore.videoDetail?.viewcount} {translate('home.video.a')} </div>
                        </div>
                        <div className="like" onClick={() => {
                            videoStore.like();
                        }}>
                            <img className='left' src={videoStore.videoDetail?.islike == 1 ? Icon_like_on : Icon_like} alt="" />
                            <div className="likeCount left">{videoStore.videoDetail?.likecount}</div>
                        </div>
                    </div>
                    <CommentPage objectid={id} objecttype={2} />
                </div>
                <div className="rightContent">
                    {videoStore.list.map((l, i) => <div key={i} onClick={() => {
                        videoStore.getDetail(l.id);
                    }} className="item">
                        <img src={l.thumb} alt="" />
                        <div className="info">
                            <div className="title">{l.title}</div>
                            <div className="view">{l.viewcount} {translate('home.video.a')}</div>
                            <div className="date">{l.createtime}</div>
                        </div>
                    </div>)}
                </div>
            </div >}

        </div >
    )
}); 