import './Header.scss';
import { Link } from '../../components/Link';
import { useState } from 'react';
import { TabsConfig } from '../../config/TabsConfig';
import { indexStore } from '../../stores/IndexStore';
import { goto } from '../../utils/globalFuncs';
import GlobalConfig from '../../config/GlobalConfig';
import Translate from '../../components/Text';

export const SideTabs = () => {
    let [link, setLink] = useState('/');
//   let hashLink = window.location.hash.slice(1);
//   link=hashLink;
  return <div className="sideBarWrapper">
        <ul className="sideBarList">
            {TabsConfig.sideTabs.map((t, i) => <li key={i} className={link == t.link ? "item active" : "item"} onClick={() => {
                setLink(t.link || '');
                indexStore.link = t.link;
                GlobalConfig.history.push(t.link ||'');
            }}>
                <img src={t.icon } alt="" />
                <Translate text={t.name} />
            </li>)}
        </ul>
    </div>
};