import { CreatorPage } from "../pages/Creator/Creator";
import { CreatorDetail } from "../pages/Creator/CreatorDetail";
import { CreatorCenterPage } from "../pages/CreatorCenter/CreatorCenter";
import { CreatorCollectionList } from "../pages/CreatorCenter/CreatorCollectionList";
import { CreatorUserList } from "../pages/CreatorCenter/CreatorUserList";
import { CreatorVideoList } from "../pages/CreatorCenter/CreatorVideoList";
import Index from "../pages/Index";
import { Main } from "../pages/Main/Main";
import { NewsPage } from "../pages/News/News";
import { PublishNewsPage } from "../pages/Publish/PublishNews";
import { PublishVideoPage } from "../pages/Publish/PublishVideo";
import { ResearchPage } from "../pages/Research/Research";
import { ResearchDetail } from "../pages/Research/ResearchDetail";
import { Subscribe } from "../pages/Subscribe/Subscribe";
import { CollectionDetailPage } from "../pages/VideosCreator/CollectionDetaill";
import { CreatorVideos } from "../pages/VideosCreator/Videos";
import { ECalendarPage } from "../pages/Market/ECalendar/ECalendar";
import { MarketIndexPage } from "../pages/Market/MarketIndex/MarketIndex";
import { PatternsPage } from "../pages/Market/Patterns/Patterns";
import { IpoCalendarPage } from "../pages/Market/IpoCalendar/IpoCalendar";
import { WatchListPage } from "../pages/Market/WatchList/WatchList";
import { StatisticPage } from "../pages/Market/Stock/Statistic/Statistic";
import { NewsDetailPage } from "../pages/News/NewsDetail";
import { RecommendPage } from "../pages/Market/Stock/Statistic/Recommend";
import { LoginIndexPage } from "../pages/Login/LoginIndex";
import { VideosPlayPage } from "../pages/Videos/VideoPlayList";
import { PatternDetail } from "../pages/Market/Patterns/PatternsDetail";
import { CompanyPage } from "../pages/Market/Stock/Statistic/CompanyInfo";
import { VideosPage } from "../pages/Videos/Videos";
import { MarketPage } from "../pages/Market/MarketIndex/MarketPage";
import { NewsIndexPage } from "../pages/News/NewsIndex";
import { StockPage } from "../pages/Market/Stock/StockPage";
import { StockIndexPage } from "../pages/Market/Stock/StockIndex";
import { FullScreenCharts } from "../pages/Market/Stock/Charts/FullScreenChart";
import { BuyGoldPage } from "../pages/BuyGold/BuyGold";
import { IndexPage } from "../pages/Index/Index";





export const routesConfig = [
    {
        path: "/",
        exact: true,
        component: IndexPage,
    },
    {
        path: "/login",
        component: LoginIndexPage,
    },
    {
        path: "/subscribe",
        component: Subscribe,
    },
    {
        path: "/stockChart",
        component: FullScreenCharts,
    },
    {
        path: "/main",
        component: Index,
        routes: [
            {
                path: "/main",
                exact: true,
                component: Main
            },
            {
                path: "/main/news",
                component: NewsIndexPage,
                routes: [
                    {
                        path: "/main/news/index",
                        component: NewsPage
                    },
                    {
                        path: "/main/news/research/:type",
                        component: ResearchPage,
                        rule: 'researchReport'
                    },

                ]
            },


            {
                path: "/main/newsDetail",
                component: NewsDetailPage
            },
            {
                path: "/main/researchDetail/:id",
                component: ResearchDetail,
                rule: 'researchReport'
            }
            ,

            {
                path: "/main/videos",
                component: VideosPage,
            },
            // {
            //     path: "/main/gold",
            //     component: MarketIndexPage,
            //     routes: [
            //         {
            //             path: "/main/gold/index",
            //             component: BuyGoldPage
            //         },]
            // },
            {
                path: "/main/gold",
                component: BuyGoldPage,
            },
            {
                path: "/main/market",
                component: MarketIndexPage,
                routes: [
                    {
                        path: "/main/market/index",
                        component: MarketPage
                    },
                    {
                        path: "/main/market/eCalendar",
                        component: ECalendarPage,
                    },
                    {
                        path: "/main/market/watchList",
                        component: WatchListPage,
                    },

                    {
                        path: "/main/market/IpoCalendar",
                        component: IpoCalendarPage,
                    },

                    {
                        path: "/main/market/latestPatterns",
                        component: PatternsPage,
                    },
                    {
                        path: "/main/market/patternDetail/:strategy",
                        component: PatternDetail,
                    },

                ]
            },


            {
                path: "/main/stock",
                component: StockIndexPage,
                routes: [
                    {
                        path: "/main/stock/statistic/:symbol",
                        component: StatisticPage,
                    },
                    {
                        path: "/main/stock/detail/:symbol/:strategy",
                        component: StockPage,
                    },
                    {
                        path: "/main/stock/recommend/:symbol",
                        component: RecommendPage,
                    },
                ]
            },

            {
                path: "/main/stockCompany",
                component: CompanyPage,
            },

            {
                path: "/main/videoPlay/:id",
                component: VideosPlayPage,
            },

            {
                path: "/main/publishVideo",
                component: PublishVideoPage,
            },
            {
                path: "/main/publishNews",
                component: PublishNewsPage,
            },
            {
                path: "/main/creatorCenter",
                component: CreatorCenterPage,
            },
            {
                path: "/main/creator",
                component: CreatorPage,
            },
            {
                path: "/main/creatorVideoList",
                component: CreatorVideoList,
            },
            {
                path: "/main/creatorCollectionList",
                component: CreatorCollectionList,
            },
            {
                path: "/main/creatorUserList",
                component: CreatorUserList,
            },

            {
                path: "/main/creatorNewsDetail",
                component: CreatorDetail,
            },
            {
                path: "/main/creatorVideos/:id",
                component: CreatorVideos,
            },
            {
                path: "/main/creatorDetail/:id",
                component: CollectionDetailPage,
            },

        ]
    },
];



