import './Videos.scss';
import { videoStore } from '../../stores/VideoStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-awesome-slider/dist/styles.css';
import Icons from '../../images/images';
import AwesomeSlider from 'react-awesome-slider';
import eventBus from '../../utils/eventBus';
import { Icon_Play } from '../../images/icons/Play';
import IntersectionImage from 'react-intersection-image';
import { verifyRule } from '../../utils/globalFuncs';
import { LocalStorageService } from '../../utils/storage';
import { Sub_Bg_Img } from '../../images/SubBg';
import { Icon_Back } from '../../images/back';
import VideoItemPage from './VideoItem';
import { Loading_icon } from '../../images/Loading';


export const VideosPage = observer((props: any) => {

    useEffect(() => {
        videoStore.getData(false);
        var middle: any = document.getElementsByClassName('middle')[0];
        let scrollFuc = (event: any) => {
            if (middle.scrollTop > (middle.scrollHeight - middle.offsetHeight - 100)) {
                videoStore.getData(true);
            }
        }
        middle.addEventListener("scroll", scrollFuc)
        return () => {
            middle.removeEventListener("scroll", scrollFuc);
        }
    }, [])
    return (
        <div className="videoPage">
            <div className="top">
                <div className="back" onClick={() => {
                    props.history.goBack()
                }}>
                    <img src={Icon_Back} alt="" />
                    <div className="text">返回</div>
                </div>

                <div className="line"></div>
                <div className="text">小课堂</div>
            </div>
            {videoStore.loading ? <div className="loading" >
                <img src={Loading_icon} alt="" />
            </div> : <div className="videoList">
                {videoStore.list.map((l, i) => <div className="item" onClick={()=>{}} key={i}> <VideoItemPage isWhite={false} item={l} /></div>)}
            </div>}
            {videoStore.loadingMore && <div className="loadmore">
                <img src={Loading_icon} alt="" />
            </div>}
        </div >
    )
});