import { ReactNode, useEffect, useState } from 'react';
import { observer } from 'mobx-react';
import { TabsConfig, TabsItem } from '../config/TabsConfig';
import '../scss/index.scss';
import { Link } from '../components/Link';
import GlobalConfig from '../config/GlobalConfig';
import { indexStore } from '../stores/IndexStore';
import { Icon_border } from '../images/index/border';
import { StockInfoTabs } from './Market/Stock/Statistic/CompanyInfo';
import { verifyRule } from '../utils/globalFuncs';
import { LocalStorageService } from '../utils/storage';
import { showError } from '../components/Message';
import Translate from '../components/Text';

interface IndexTabsProps {
  tabs: TabsItem[];
  rightNode?: ReactNode
}
export const IndexTabs = observer((props: IndexTabsProps) => {
  let hashLink = window.location.hash.slice(1);
  indexStore.link = hashLink;
  return (
    <div className="indexTabs" >
      <div className="tabContent">
        {
          props.tabs.map((t, i) => {
            let getLink = t.getLink && t.getLink();
            return <div key={i} className={indexStore.link == (getLink || t.link) ? 'tabActive' : 'tab'} onClick={() => {
              if (t.needLogin) {
                let isLogin = LocalStorageService.get("token");
                if (!isLogin) {
                  GlobalConfig.history.push('/login');
                  return;
                }
              }


              var link = '';
              if (t.getLink) {
                link = t.getLink();
              } else if (t.link) {
                link = t.link;
              }
              indexStore.link = link;
              if (t.verify) {
                verifyRule('researchReport', link)
              } else {
                GlobalConfig.history.push(link);
              }
            }}>
              <Translate text={t.name} />
            </div>
          })
        }
      </div>
      {props.rightNode && props.rightNode}
    </div>
  );
});

export default IndexTabs;
