import { observer } from 'mobx-react';
import { useEffect, useState } from 'react';
import StatisticStore from '../../../stores/StatisticStore';
import './MarketIndex.scss';
import { Movements } from '../../../services/WatchlistService';
import { useHistory } from 'react-router';
import { StockLogo } from '../../../components/StockLogo';
import { Loading_icon } from '../../../images/Loading';
import { indexStore } from '../../../stores/IndexStore';
import Translate from '../../../components/Text';



export const MarketPage = observer(() => {
    let history = useHistory();
    useEffect(() => {
        StatisticStore.getWatchMarketlist();
    }, []);
    const tabs = ['markets.b', 'markets.c'];
    return (
        <div className="marketCenterPage">
            {StatisticStore.marketLoading ? <div className="loading">
                <img src={Loading_icon} alt="" />
            </div> : <div className="content">
                {tabs.map((t, index) => {
                    let _list: Array<Movements> = (index == 0 ? StatisticStore.markets?.top_movements?.top_10 : StatisticStore.markets?.top_movements?.last_10) || [];
                    let list: Array<Movements> = JSON.parse(JSON.stringify(_list));
                    let listCount: number = list.length;
                    if (list.length < 12) {
                        let count: number = 12 - list.length;
                        for (var i = 0; i < count; i++) {
                            list.push(list[0]);
                        }
                    }
                    return <div key={index} className="contentWrapper">
                        <div className="topInfo">
                            <div className="title"><Translate text={t} /></div>

                            <div className="dealDate"><Translate text={'markets.d'} args={{"date":StatisticStore.markets?.top_movements.date}}/> </div>
                        </div>
                        <ul className="marketCenterContent">
                            {list.map((item, idx) => {
                                if (idx > (listCount - 1)) {
                                    return <div className="item" style={{ background: "white", cursor: "auto" }}></div>
                                }
                                return <div className="item" key={idx} onClick={() => {
                                    window.scrollTo(0, 0);
                                    StatisticStore.symbol = item.symbol_id;
                                    indexStore.link = '/stock/statistic';
                                    history.push(`/stock/statistic/${item.symbol_id}`);
                                }}>
                                    <div className="info">
                                        <StockLogo symbol={item.symbol_id} />
                                        <div className="text">
                                            <div className="symbol">{item.symbol_id.split('_')[0]}</div>
                                            <div className="name">{item.symbol_name}</div>
                                        </div>
                                    </div>
                                    <div className="price">
                                        <div className="left">{item.price}</div>
                                        <div className="right" style={{ color: index > 0 ? '#EC4D3D' : '#4CB94D' }}>{(item.daily_return * 100).toFixed(2)}%</div>
                                    </div>
                                </div>
                            })
                            }
                        </ul>
                    </div>
                }
                )}
            </div>}
        </div >
    )
});