import { LoginModel, loginStore } from '../../stores/LoginStore';
import './Login.scss';
import { Icon_logo } from '../../images/index/logo';
import { LoginPage } from './Login';
import { RegisterPage } from './Register';
import { ForgetPasswordPage } from './ForgetPassword';
import { observer } from 'mobx-react';
import { Headers } from '../Header/Header';
import { translate } from '../../components/Text';

interface loginProps {
    close?: Function;
}

export const LoginIndexPage = observer((props: loginProps) => {
    var title;
    if (loginStore.LoginModel === LoginModel.login) {
        title=translate('mine.login.h') ;
    } else if (loginStore.LoginModel === LoginModel.register  || loginStore.LoginModel === LoginModel.registerPop) {
        title=translate('mine.login.g');
    } else {
        title = translate('mine.login.f');
    }
    return (
        <div className="loginPage">
      <Headers />
            <div className="loginWrapper">
                <div className="logo">
                    <img src={Icon_logo} alt="" />
                    <div className="name">Oli Views</div>
                </div>
                <div className="loginContent">
                    <div className="title">{title}</div>
                    {loginStore.LoginModel === LoginModel.login ?
                        <LoginPage /> : loginStore.LoginModel === LoginModel.register ?
                            <RegisterPage /> : <ForgetPasswordPage />}
                </div>
            </div>
        </div>)
})