import { Form, Input, Button } from 'antd';
import { MailOutlined, LockOutlined } from '@ant-design/icons';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import './Login.scss';
import { Icon_logo } from '../../images/index/logo';
import { userStore } from '../../stores/UserStore';
import GlobalConfig from '../../config/GlobalConfig';
import { translate } from '../../components/Text';
import { observer } from 'mobx-react';

interface loginProps {
    close?: Function;
}

export const LoginPage = observer((props: loginProps) => {
    return (
        <div className="info">
            <Form
                name="normal_login"
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={(v) => loginStore.login(v, () => {
                    GlobalConfig.history.goBack();
                })}
            >
                <Form.Item
                    name="email"
                    rules={[{ required: true, message:translate('mine.login.c') }]}
                >
                    <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder={translate('mine.login.c')}
                    />
                </Form.Item>
                <Form.Item
                    name="password"
                    rules={[{ required: true, message:translate('mine.login.e') }]}
                >
                    <Input
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder={translate('mine.login.e')}
                    />
                </Form.Item>
                <Form.Item>
                    <Button htmlType="submit" className="login-form-button">
                        {translate('mine.login.h')}
                    </Button>
                </Form.Item>
                <div className="form-wrapper">
                    <a className="login-form-forgot" href="javascript:void(0);" onClick={() => {
                        if (loginStore.loginLoading) return;
                        loginStore.setLoginModel(LoginModel.forgetPassword)
                    }}>
                        {translate('mine.login.f')}
                    </a>
                    <a className="login-form-register" href="javascript:void(0);" onClick={() => {
                        if (loginStore.loginLoading) return;
                        loginStore.setLoginModel(LoginModel.register)
                    }}>
                        
                        {translate('mine.login.g')}

                    </a>
                </div>
            </Form>
        </div>
    );
})













