import { makeAutoObservable, } from "mobx";
import { setInterval } from "timers";
import { showError, showLoading, showSuccess } from "../components/Message";
import { RegisterService, UserInfo } from "../services/LoginService";
import { LocalStorageService } from '../utils/storage';

class MainStore {
  public swiperIndex: number = 0;
  public qrcode: string = 'ios';
  public isEn:boolean= false;
  private registerLoading:boolean = false;
  private interval: any;
  constructor() {
    makeAutoObservable(this);
  }
   
  public async visitorRegister(email:string){
    let useName = 'User' + Math.random().toString(26).substr(8);
    if (this.registerLoading) {
      return;
    }
    this.registerLoading = true;
      showLoading( !mainStore.isEn ? '提交中...' : 'Submitting...');
    try {
      await new RegisterService({
        email:email,
        username:useName
      }).send();
  showSuccess(!mainStore.isEn ? '提交成功！' : 'Submit successfully');
} catch (e: any) {
      this.registerLoading = false;
  showSuccess(!mainStore.isEn ? '提交成功！' : 'Submit successfully');
      return;
    }
    this.registerLoading = false;
  }

  public init() {
    if (this.interval) {
      return;
    }
    this.interval = setInterval(() => {
      if (this.swiperIndex < 3) {
        this.swiperIndex += 1;
      } else {
        this.swiperIndex = 0;
      }
    }, 4000);
  }


}

const mainStore = new MainStore();

export { mainStore };