import { ResearchItem } from '../../services/ResearchService';
import { EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import '../Main/Main.scss';
import { verifyRule } from '../../utils/globalFuncs';
import { indexStore } from '../../stores/IndexStore';
import { Icon_collect } from '../../images/market/collect';
import { Icon_collect_on } from '../../images/market/collectOn';
import { Icon_view } from '../../images/market/view';
import { VideoItem } from '../../services/VideoService';
import GlobalConfig from '../../config/GlobalConfig';
import { Icon_collect_white } from '../../images/market/collectWhite';
import { LocalStorageService } from '../../utils/storage';
import { showError } from '../../components/Message';

interface ReseatchItemProps {
  item: VideoItem;
  isWhite: boolean
}

function VideoItemPage(props: ReseatchItemProps) {
  var item: VideoItem = props.item;
  return (
    <div className="videoItem" onClick={() => {
      if (item.isfree != '1') {
        let isLogin = LocalStorageService.get("token");
        if (!isLogin) {
          GlobalConfig.history.push('/login');
          return;
        }
        GlobalConfig.history.push(`/videoPlay/${item.id}`);
      } else {
        verifyRule('researchReport', `/videoPlay/${item.id}`)

      }
    }}>
      <img className='bg' src={item.thumb} alt="" />
      <div className="info">{item.title}</div>
      <div className={props.isWhite ? "viewsWhite" : "views"}>
        <img className='like' src={item.isfollow === 1 ? Icon_collect_on : (props.isWhite ? Icon_collect_white : Icon_collect)} alt="" />
        <div className="likeCount">{item.likecount}</div>
        <EyeOutlined className="icon-gary" style={{ color: props.isWhite ? '#fff' : '#999' }} />
        <div className="viewCount">{item.viewcount}</div>
      </div>
    </div>
  );
}

export default VideoItemPage;

