import { Form, Input, Button } from 'antd';
import { observer } from 'mobx-react';
import { PhoneOutlined, LockOutlined, MailOutlined } from '@ant-design/icons';
import { LoginModel, loginStore } from '../../stores/LoginStore';
import './Login.scss';
import { translate } from '../../components/Text';

interface ForgetPasswordProps {
    close?: Function;
}


export const ForgetPasswordPage = observer((props: ForgetPasswordProps) => {
    let email: string = '';
    return (
        <div className="info">
        <Form
            name="normal_login"
            className="login-form"
            initialValues={{ remember: true }}
            onFinish={(v) => loginStore.findPassword(v)}
        >

            <Form.Item
                name="email"
                rules={[{ required: true, message: translate('mine.register.d') }]}
            >
                <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder={translate('mine.register.d')} onChange={(e) => { email = e.target.value }} />
            </Form.Item>
            <div className="captcha">
                <Form.Item
                    className="codeInput"
                    name="captcha"
                    rules={[{ required: true, message:translate('mine.find.c') }]}
                >
                    <Input prefix={<PhoneOutlined  className="site-form-item-icon" />} placeholder={translate('mine.find.c')} />
                </Form.Item>
                <Button htmlType="submit" className="forget-form-button" onClick={() => { loginStore.sendEmial(email) }}>
                    {loginStore.sendBtnText}
                </Button>
            </div>

            <Form.Item
                name="password"
                rules={[{ required: true, message: translate('mine.register.f') }]}
            >
                <Input
                    prefix={<LockOutlined className="site-form-item-icon" />}
                    type="password"
                    placeholder={translate('mine.register.f') }
                />
            </Form.Item>
                <Button htmlType="submit" className="login-form-button">
                   {translate('mine.l') }
                </Button>
        </Form>
        </div>
    );
});













