import './Research.scss';
import { ResearchDetailStore } from '../../stores/ResearchDetailStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Icon_Back } from '../../images/back';
import { Loading_icon } from '../../images/Loading';
import { Icon_collect_on } from '../../images/market/collectOn';
import { Icon_collect } from '../../images/market/collect';

let researchDetailStore: ResearchDetailStore = new ResearchDetailStore();
export const ResearchDetail = observer((props: any) => {
    let id = props.match.params.id;
    useEffect(() => {
        researchDetailStore.getData(id);
    }, [id]);
    return (
        <div className="researchDetailWrapper">
            <div className="tabs">
                <img className="back" src={Icon_Back} alt="" onClick={() => {
                    props.history.goBack()
                }} />

                <div className="title">{researchDetailStore.detail?.title}</div>

                <div className="items">
                    <img className='follow' src={researchDetailStore.detail?.isfollow == 1?Icon_collect_on: Icon_collect} alt="" onClick={() => {
                        researchDetailStore.follow();
                    }} />
                </div>
            </div>
            {researchDetailStore.loading ? <div className="loading" >
                <img src={Loading_icon} alt="" />
            </div> : <div className="researchDetailContent" >

                <div className="pdfWrapper">
                    <div className="pdf" style={{ height: researchDetailStore.pdfHeight + 'px' }}>
                        <div id="viewerContainer" >
                            <div id="viewer" className="pdfViewer"></div>
                        </div>
                    </div>
                </div>

            </div>}
        </div>
    )
});