import { subscribeStore } from '../../stores/SubscribeStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import './Subscribe.scss';
import { Modal } from 'antd';
import { Loading } from '../../components/Loading';
import { LocalStorageService } from '../../utils/storage';
import { userStore } from '../../stores/UserStore';
import { Icon_Checked } from '../../images/checked';
import { Headers } from '../Header/Header';
import { showError } from '../../components/Message';

export const Subscribe = observer(() => {
    useEffect(() => {
        subscribeStore.getData();
    }, []);
    const titles = ['付费平台信息阅读权限', '傲利个股股评阅读权限', '傲利深度研究报告阅读权限', '行业季度报告优先阅读权限', '一对一分析师', '技术形态分析'];
    return (
        <>
            <Headers />
            <div className="subscribeWrapper">
                <div className="title">会员与订阅服务</div>
                {subscribeStore.loading ? <Loading /> : <div className="list">
                    {subscribeStore.list.map((l, idx) => <div key={idx} className="item">
                        <div className="title">{l.name}</div>
                        <div className="price">{l.price}</div>
                        <div className="description">{l.desc}</div>
                        <div className="infos">
                            {titles.map((t, i) => <div className="info">
                                <img src={Icon_Checked} />
                                <div className="text">{t}</div>
                            </div>)}
                        </div>

                        <div className="pay" onClick={() => {
                            let isLogin = LocalStorageService.get("token");
                            if (isLogin) {
                                if (l.isSubscribe === 1) {
                                    if (l.isRenew === 1) {
                                        subscribeStore.showConfirm = true;
                                    } else {
                                        subscribeStore.activateSubscribe(l.priceid);
                                    }
                                } else {
                                    subscribeStore.subscribe(l.priceid);
                                }
                            } else {
                                showError('请登录后操作！');
                            }
                        }}>
                            {subscribeStore.subscribeLoadingId === l.priceid ? <Spin indicator={<LoadingOutlined style={{ fontSize: 15 }} />} /> : (l.isSubscribe === 1 ? (l.isRenew ? '自动续费中' : '激活') : '订阅')}
                        </div>

                    </div>)}

                </div>}

                <Modal
                    title="提示"
                    visible={subscribeStore.showConfirm}
                    onOk={subscribeStore.cancelSubscribe.bind(subscribeStore)}
                    confirmLoading={subscribeStore.confirmLoading}
                    onCancel={() => subscribeStore.showConfirm = false}
                    okText='确定'
                    cancelText='取消'
                    centered={true}

                    width={300}
                >
                    <p>是否确定取消自动续费?</p>
                </Modal>
            </div>
        </>
    )
});