
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { newsStore } from '../../stores/NewsStore';
import './News.scss';
import { Loading_icon } from '../../images/Loading';
import { NewsItem } from './NewsItem';
import Translate from '../../components/Text';

export const NewsPage = observer((props: any) => {
  useEffect(() => {
    newsStore.getData();
    var middle: any = document.getElementsByClassName('middle')[0];
    let scrollFuc = (event: any) => {
      if (middle.scrollTop > (middle.scrollHeight - middle.offsetHeight - 100)) {
        newsStore.getData(true);
      }
    }
    middle.addEventListener("scroll", scrollFuc)
    return () => {
      middle.removeEventListener("scroll", scrollFuc);
    }
  }, [])
  return (
    <div className="NewsPage">
      <div className="newsContent">
        {newsStore.loading ? <div className="loading" >
          <img src={Loading_icon} alt="" />
        </div> :
          <div className='newsList'>
            <div className="list">
              {newsStore.newsList.filter((n, i) => i % 2 == 0).map((n, i) =>
                <div key={i} >
                  <NewsItem title={n.title} history={props.history} image={n.thumb} url={n.url} content={n.url ? (n.abstract || n.content) : n.content} source={n.source} createtime={n.createtime} />
                </div>)}
            </div>

            <div className="list">
              {newsStore.newsList.filter((n, i) => i % 2 != 0).map((n, i) =>
                <div key={i} >
                  <NewsItem title={n.title} history={props.history} image={n.thumb} url={n.url} content={n.url ? (n.abstract || n.content) : n.content} source={n.source} createtime={n.createtime} />
                </div>)}
            </div>
          </div>


        }
        {newsStore.loadingMore && <div className="loadmore">
          <img src={Loading_icon} alt="" />
        </div>}
      </div>
    </div>
  );
}
)